import React, {Component} from 'react';
import {connect} from 'react-redux';
import {INSTAGRAM_ID} from '../../actions/const';
import InstagramComponent from '../../components/Social/Instagram/Instagram';

class Instagram extends Component {

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <InstagramComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.item[INSTAGRAM_ID]
    }
}

export default connect(mapStateToProps)(Instagram);