import React, {Component} from 'react';
import {connect} from 'react-redux';
import { toggleItem } from '../../actions/index';
import {changeItem} from '../../actions/index';


class Settings extends Component {

    changeState = () => {
        const ul = document.getElementById("settings__ul");
        ul.addEventListener('click', (event) => {
            let target = event.target;
            if(target.tagName !== "LI")
                return;
            target = target.getAttribute("data-key");
            this.props.onChangeState(target);
            localStorage.setItem('store', JSON.stringify(this.props.localState))
        })
    }

    toggleSettings = () => {
        const settings = document.getElementById("settings__ul");
        const mainContent = document.getElementById("main-content");
        const social = document.getElementById("settings__social");
        const icon = document.getElementById("settings__img");
        icon.addEventListener('click', (event) => {
            settings.classList.toggle("hidden");
            social.classList.toggle("hidden");
            mainContent.classList.toggle("hidden");
            event.preventDefault();
        })
    }

    addLink = () => {
        const social = document.getElementById("settings__social");
        social.addEventListener('click', (event) => {
            let target = event.target;
            if (target.tagName !== "INPUT")
                return;
            target.placeholder = "";
            target.value = "";
        })
    }

    saveChanges = () => {
        const button = document.getElementById("settings__social__add");
        button.addEventListener('click', (event) => {
            const socialItems = document.querySelectorAll(".settings__social__item input");
            socialItems.forEach(i => {
                if (i.value.indexOf("http://") == 0 || i.value.indexOf("https://") == 0) {
                    this.props.onChangeItem(i.getAttribute("data-key"), i.value)
                }
                else {
                    i.value = "please, write correct web address"
                }
            });
        });
    }
    componentDidMount() {
        this.changeState();
        this.toggleSettings();
        this.addLink();
        this.saveChanges();
    }

    render() {
        return(
            <section id = "settings">
                <div className="flex-center-center">
                    <ul id = "settings__ul" className = "hidden">
                    <span>Вы можете настроить отображение</span>
                    {
                        this.props.localState.item.map((element, index) => 
                        <li 
                            key = {element.id}
                            data-key = {element.id}
                            style = {{textDecoration: element.status ? 'none' : 'line-through' }}>
                            {element.name}
                        </li>)
                    }
                    </ul> 
                </div>
                <div className="flex-center-center">
                    <div id = "settings__social" className = "hidden">
                        {
                            this.props.social.map(item => 
                                <div key = {item.id} className = "settings__social__item">
                                    {item.name}
                                    <input type="text" placeholder = {item.value} data-key = {item.id}/>
                                </div>
                            )
                        }
                        <button className = "button button-dark" id = "settings__social__add">
                            save changes
                        </button>
                    </div>
                </div>
            </section>
        )
    }
}

function mapStateToProps(state) {
    return {
        localState: state,
        social: state.item.filter(item => item.idCategory === 2)
    }
}

function mapDispatchToProps(dispatch) {
    return {
        onChangeState : (id) => {
            dispatch(toggleItem(id))
        },

        onChangeItem: (id, value) => {
            dispatch(changeItem(id, value))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Settings)