import React, {Component} from 'react';


const Welcome = () => {
    return(
        <section id = "welcome" className="flex-center-center">
                <div id = "welcome__item">
                    <h2 className = "white">
                        Добро пожаловать в All-in-one!
                    </h2>
                    <p>
                        мы сохраняем все ваши настройки в localStorage,
                        поэтому вам не надо регистрироваться и если вы почистите
                        кэш браузера, то все настройки пропадут)
                    </p>
                    <div className="flex-center-center">
                        <button id = "welcome__reload" className = "button button-white">
                            Принять
                        </button>
                    </div>
                </div>
        </section>
    )
}

export default Welcome