import React, {Component} from 'react';
import {connect} from 'react-redux';
import {VK_ID} from '../../actions/const';
import VKComponent from '../../components/Social/VK/VK';

class VK extends Component {

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <VKComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.item[VK_ID]
    }
}

export default connect(mapStateToProps)(VK);